import Vue from 'vue'
import Vuex from 'vuex'
import router from './router'
import axios from 'axios'
import {isNull} from "bootstrap-vue/esm/utils/inspect";
import {from} from "bootstrap-vue/esm/utils/array";

// test it.
Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        userInfo: null,
        isLogin: false,
        isLoginError: true
    },
    mutations: {
        //login success
        loginSuccess(state){
            state.isLogin = true,
            state.isLoginError = false
        },
        //login fail
        loginError(state){
            state.isLogin = false,
            state.isLoginError = true
        },
        logout(state) {
            state.isLogin = false,
            state.isLoginError = false,
            sessionStorage.removeItem("access_token");

        }
    },
    actions:{
        //try login
        login({ commit}, loginObj) {
          sessionStorage.setItem("userid", loginObj["userid"])
            // Login => token return
            axios
                .post("http://127.0.0.1:5000/login", loginObj)
                .then(res => {
                  console.log("test")
                    let response = res.data
                    if(response['code'] == '200')
                        {
                        sessionStorage.setItem("access_token", response['access_token'])
                        let token = sessionStorage.getItem("access_token")
                        let config = {
                            headers: {
                                "Authorization": token
                                }
                            }
                            commit('loginSuccess')
                            router.push("/")
                        }
                    else {
                        alert(' Check your Id&PW ');
                        }
                })
                .catch(() => {
                           alert(' Check your Id&PW ');
                       })
            },
        loginRefresh( {commit} ) {
            let token = sessionStorage.getItem("access_token");
            if (token != null) {
                commit("loginSuccess")
                router.push("/")
            }
            else
                commit('loginError')

        },
        logout({commit}) {
                commit("logout")
                router.push("/login")
            },
    }
})



